/* Overview map filters */

.filters {
    width:100%;
    background-color: $blue_light;
    padding: 0 10px;
    height: auto;
    text-align: center;
    z-index: 2;
}

.filters .filter-toggle {
    display: block;
    padding: 12px 0;
    width: 100%;
    font-family: "Open sans";
    font-size: 18px;
    text-decoration: none;
    color: #fff;
}

.filters .filter-toggle:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    margin: 0 8px 0 0;
}

.filters .filters-wrap {
    display: block;
    max-height: 0;
    height: 100%;
    -webkit-transition:max-height .6s;
    transition: max-height .6s;
    overflow: hidden;
    width: 100%;
}

.filters .filters-wrap.open {
    max-height: 300px;
}

.filters .form {
    margin: 15px 0 0 0;
}

.filters .field {
    margin: 0 0 10px;
}

.filters p {
    display: block;
    color: #fff;
    font-family: "Open sans";
    font-weight: 600;
    font-size: 18px;
    letter-spacing: -0.2px;
}

.filters p, .filters .field, .filters .button {
    margin-left: 5px;
    margin-right: 5px;
}

.filters .container-select {
    background-color: $blue;
}

.filters .container-select:before {
    background-color: $blue_dark;
    border: none;
}

.filters .container-select:after {
    border-top-color: #fff;
    right: 12px;
}

.filters .container-select select {
    border: none;
    border-radius: 0;
    color: #fff;
    padding-left: 20px;
    margin-left: 0;
    margin-right: 0;
}

/* media queries for .filters */

@media (min-width: $(tablet)) {

    .filters {
        display:-webkit-box;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
    }

    .filters .form,
        .filters .field {
        display:inline-block;
    }
}

@media (min-width: $(desktop)) {

    .filters {
        padding:20px 10px;
    }

    .filters .filter-toggle {
        display:none;
    }

    .filters .filters-wrap {
        max-height:initial;
        overflow:visible;
    }
}

@media (min-width: $desktop-large) {

    .filters {
        height:67px;
        padding:20px 0;
        text-align:left;
    }

    .filters .filters-wrap {
        display:-webkit-box;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
        -webkit-box-align:center;
        -webkit-align-items:center;
            -ms-flex-align:center;
                align-items:center;
        -webkit-box-orient:horizontal;
        -webkit-box-direction:normal;
        -webkit-flex-direction:row;
            -ms-flex-direction:row;
                flex-direction:row;
        -webkit-box-pack:center;
        -webkit-justify-content:center;
            -ms-flex-pack:center;
                justify-content:center;
        width:100%;
    }

    .filters .form {
        margin-top:0;
    }

    .filters .field {
        margin-bottom:0;
    }

    .filters p, .filters .field, .filters .button {
        margin-left:10px;
        margin-right:10px;
    }
}